export default {
	name: "tuiNumberbox",
	props: {
		value: {
			type: Number,
			default: 1
		},
		//最小值
		min: {
			type: Number,
			default: 0
		},
		//最大值
		max: {
			type: Number,
			default: 100
		},
		//迈步大小 1 1.1 10...
		step: {
			type: Number,
			default: 1
		},
		//是否禁用操作
		disabled: {
			type: Boolean,
			default: false
		},
		//加减图标大小 rpx
		iconsize: {
			type: Number,
			default: 24
		},
		iconcolor: {
			type: String,
			default: "#333"
		},
		//input 高度
		height: {
			type: Number,
			default: 50
		},
		//input 宽度
		width: {
			type: Number,
			default: 90
		},
		//input 背景颜色
		bgcolor: {
			type: String,
			default: "#f2f2f2"
		},
		//input 字体颜色
		color: {
			type: String,
			default: "#333"
		}
	},
	computed: {
		val() {
			return this.value
		}
	},
	data() {
		return {

		};
	},
	methods: {
		px(num) {
			return uni.upx2px(num) + "px"
		},
		getScale() {
			let scale = 1;
			//浮点型
			if (!Number.isInteger(this.step)) {
				scale = Math.pow(10, (this.step + '').split('.')[1].length)
			}
			return scale;
		},
		calcNum: function(type) {
			if (this.disabled) {
				return
			}
			const scale = this.getScale()
			let num = this.value * scale
			let step = this.step * scale
			if (type === 'reduce') {
				num -= step
			} else if (type === 'plus') {
				num += step
			}
			let value = num / scale
			if (value < this.min || value > this.max) {
				return
			}
			console.log(value)
			this.handleChange(value, type)
		},
		plus: function() {
			this.calcNum("plus")
		},
		reduce: function() {
			this.calcNum("reduce")
		},
		blur: function(e) {
			let value = e.detail.value
			if (value) {
				value = +value
				if (value > this.max) {
					value = this.max
				} else if (value < this.min) {
					value = this.min
				}
			} else {
				value = this.min
			}
			this.handleChange(value, "blur")
		},
		handleChange(value, type) {
			if (this.disabled) {
				return
			}
			this.$emit('change', {
				value: value,
				type: type
			})
		}
	}
}
